import React from "react";
import Score from "./Score"
import { GiSandsOfTime } from "react-icons/gi";
import "../styles/_card.scss"

const CardBack = ({content}) => {
 return (
     <div className="card--back">
        <h2>{content.nombre}</h2>
        <p>Puntuación: <Score score={content.puntuacion} icono="heart" /></p>
        <p>Dificultad: <Score score={content.dificultad}  icono="star"/></p>
        <p>Tiempo: <span> {content.tiempo} <GiSandsOfTime /></span></p>
     </div>
 )
};

export default CardBack;

