import React from "react";
import CardFront from "./CardFront";
import CardBack from "./CardBack";
 import "../styles/_card.scss"

const Card = ({item}) => {
   
 return (
    <div className="card" >
        <div className="card--inner">
            <CardFront detail={item} />
        
       
            <CardBack content={item} />
        </div>
    </div>
 )
};


export default Card;