import React from "react";
import "../styles/_card.scss"

const CardFront = ({detail}) => {
  let img= detail.img;
 return (
    <div className="card--front" >
        <img  src={detail.img} alt=""/>
        
        {detail.nuevo && <span className="tag tag--new-recipe">Nuevo</span> }
        {detail.sinAzucar && <span className="tag tag--no-sugar">Sin azúcar</span> }
    </div>
 
 )
};

export default CardFront;